<template>
  <div class="login">
    <header class="login-tit">
      <div class="login-container">
        <img class="logo" src="../assets/images/logo.png" alt="">
      </div>
    </header>
    <div class="login-main">
      <div class="main">
        <div class="left">
          <h2>优加健康后台系统</h2>
          <ul>
                <li>
                  <div class="field pr">
                      <label class="text name" for=""></label>
                      <input class="ipttxt" type="text" placeholder="用户名" v-model="username">
                  </div>
                </li>
                <li>
                  <div class="field pr">
                      <label class="text psd" for=""></label>
                      <input class="ipttxt" type="password" placeholder="密码" v-model="password" @keyup.enter="login()">
                  </div>
                </li>
                <li>
                  <input type="submit" class="loginBtn" v-on:click="login()" value="登录">
                </li>
            </ul>
        </div>
        <!-- <div class="login-right">
          <div class="loginModule">
            <h2>优加健康后台系统</h2>
            <ul>
                <li>
                  <div class="field pr">
                      <label class="text name" for=""></label>
                      <input class="ipttxt" type="text" placeholder="用户名" v-model="username">
                  </div>
                </li>
                <li>
                  <div class="field pr">
                      <label class="text psd" for=""></label>
                      <input class="ipttxt" type="password" placeholder="密码" v-model="password" @keyup.enter="login()">
                  </div>
                </li>
                <li>
                  <input type="submit" class="loginBtn" v-on:click="login()" value="登录">
                </li>
            </ul>
          </div>
        </div> -->
      </div>
    </div>
    <div class="footer"> 
      <span>优加健保健康科技（北京）有限公司</span>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
import md5 from 'md5';
const queryString = require('querystring')
import validate from '../utils/validate.js';
export default {
        name: '',
        mixins: [validate],
        data() {
            return {
              username:'', //账户
              password:'', //密码
              validateData: {},
            }
        },
        created(){
           
        },
        mounted(){
          
        },
        computed:{

        },
        methods:{
          login(){

            this.setValidateData();
            if (!this.validateEngine(this.validateData)) return;
            var param = {
                  'account':this.username,
                  'password':md5(this.password).toLowerCase()
                };
            this.axios.post('/api/authserver/user/login', queryString.stringify(param)).then((res) => {
              const data = res.data
              if (String(data.code) === '200') {
                const token = data.data
                this.$store.commit('setToken', token)
                localStorage.setItem('token', token)
                localStorage.setItem('username', this.username)
                this.$router.push('/')
              } else {
                this.$message(data.message || this.mes.busy)
              }
            }).catch((error) => {
              console.log(error)
            });
          },
          // 校验引擎
          validateEngine: function (data) {
            for (var i = 0; i < data.length; i++) {
              //console.log(this[data[i].type]);
              if (!this[data[i].type](data[i].value)) {
                // 提示具体的错误信息
                this.$message(data[i].info);
                return false;
              }
            }
              return true;
          },
          // 配置校验数据
          setValidateData: function () {
            this.validateData = [
              {
                type: 'isNoEmpty',
                value: this.username,
                info: '账户不能为空'
              },{
                type: 'isNoEmpty',
                value: this.password,
                info: '密码不能为空'
              },
            ];
          }
        }
    }
</script>

